// Center block
@mixin center-block {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

// Column width with margin
@mixin column-width($numberColumns: 3) {
  width: map-get($columns, $numberColumns) -
    (($columns__margin * ($numberColumns - 1)) / $numberColumns);
}

@mixin flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin container-large {
  max-width: $container__large;
  margin: 0 auto;
  padding: 0 1.25rem;

  @media only screen and (min-width: $desktop__full) {
    padding: 0;
  }
}
